import React, { MouseEvent, KeyboardEvent } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Socket from '../../lib/socket';

// lib
import { fetchBefore } from '../../lib/api';
import { updateChatHistory } from '../../store/talk';

interface Props {
    history: any;
    room: string;
    updateChatHistory: Function;
    chat_history: Array<any>;
}


class TalkingRoom extends React.PureComponent<Props> {
    render() {
        console.log(this.props.chat_history);
        return (
            <div>
                {/* 채팅 뷰터*/}
                <div>
                {
                    this.props.chat_history.map((chat, index) => {
                        return (
                            <div key={ index.toString() }>
                                <p>{ chat }</p>
                            </div>
                        )
                    })
                }
                </div>
                {/* 채팅 */}
                <div>
                    <input 
                        name='chat'
                        placeholder='입력하세요...'
                        // onSubmit={ this.onChat }
                    />
                </div>
            </div>
        )
    }

    componentDidMount = () => {
        const { state } = this.props.history.location;
        if (state && state.room_id) {
            this.setState({ room: state.room_id }, () => {
                this.fetchBeforeChat(state.room_id);
            });
        } else {
            this.props.history.push('/talk');
        }
    }

    fetchBeforeChat = async (room_id: string) => {
        const { data } = await fetchBefore(room_id);
        this.props.updateChatHistory(data);
    }

    onChat = async (e: MouseEvent|KeyboardEvent) => {
        e.preventDefault();
        
        console.log(e)
        // socket - flash
        Socket.emit();
        
        // mongodb - save


    }
}

const mapStateToProps = ({ talkReducer }: any) => ({
    chat_history: talkReducer.room_chat_history,
});

const mapDispatchToProps = (dispatch: any) => ({
    updateChatHistory: (chat: Array<any>) => dispatch(updateChatHistory(chat))
});

export default withRouter(
    connect(mapStateToProps, mapDispatchToProps)(TalkingRoom) as any
);