
// type
const UPDATE_ROOMS = 'UPDATE_ROOMS';
const UPDATE_BEFORE_CHAT = 'UPDATE_BEFORE_CHAT';

// action by type
export const updateRooms = (rooms: Array<any>) => ({ type: UPDATE_ROOMS, rooms });
export const updateChatHistory = (chat: Array<any>) => ({ type: UPDATE_BEFORE_CHAT, chat });

interface stateTypes {
    rooms: Array<any>;
    room_chat_history: Array<any>;
};

const initialState: stateTypes = {
    rooms: [],
    room_chat_history: [],
};

// case
export default function (state=initialState, action: any) {
    switch(action.type) {
        case UPDATE_ROOMS:
            return {
                ...state,
                rooms: action.rooms,
            };
        case UPDATE_BEFORE_CHAT:
            return {
                ...state,
                room_chat_history: state.room_chat_history.concat(action.chat),
            };
        default:
            return state;
    }
};